import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import classnames from 'classnames'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardMedia from '@material-ui/core/CardMedia'
import CardContent from '@material-ui/core/CardContent'
import CardActions from '@material-ui/core/CardActions'
import Collapse from '@material-ui/core/Collapse'
import Avatar from '@material-ui/core/Avatar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import FavoriteIcon from '@material-ui/icons/Favorite'
import ShareIcon from '@material-ui/icons/Share'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import { getImageMovie } from '../../common/utils/helpers'
import Link from '../../components/Link'
import moment from 'moment'

const styles = theme => ({
  card: {
    maxWidth: 400
  },
  media: {
    height: 0,
    paddingTop: '56.25%' // 16:9
  },
  actions: {
    display: 'flex',
    justifyContent: 'space-between'
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: 'rotate(180deg)'
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main
  }
})

class MovieCard extends Component {
  state = { expanded: false }

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }))
  }

  render() {
    const { classes, movie } = this.props
    const { uploader = {} } = movie
    return (
      <Card className={classes.card}>
        {/* <CardHeader
          avatar={
            <Avatar aria-label="Recipe" className={classes.avatar}>
              R
            </Avatar>
          }
          action={
            <IconButton>
              <MoreVertIcon />
            </IconButton>
          }
          title="Shrimp and Chorizo Paella"
          subheader="September 14, 2016"
        /> */}
        <CardMedia className={classes.media} image={getImageMovie(movie)} title="Paella dish" />
        <CardContent>
          <Typography>
            <Link variant="subtitle1" href={`/movies/${movie._id}`} color="textPrimary">
              {movie.name}
            </Link>
          </Typography>
          <Typography>
            <Link variant="caption" href={`/profile/${uploader._id}`} color="textPrimary">
              {uploader.name}
            </Link>
          </Typography>
          <Typography variant="caption" color="textPrimary">
            Link
          </Typography>
        </CardContent>
        <CardActions className={classes.actions} disableActionSpacing>
          {/* <IconButton aria-label="Add to favorites">
            <FavoriteIcon />
          </IconButton>
          <IconButton aria-label="Share">
            <ShareIcon />
          </IconButton> */}

          <Typography align="left" inline={true} variant="caption" color="textPrimary">
            {movie.viewsCount} views
            {moment(movie.createdAt)
              // .startOf(movie.createdAt)
              .fromNow()}
          </Typography>
          <IconButton
            // className={classnames(classes.expand, {
            // [classes.expandOpen]: this.state.expanded
            // })}
            // onClick={this.handleExpandClick}
            // aria-expanded={this.state.expanded}
            aria-label="Show more"
          >
            <MoreVertIcon />
          </IconButton>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent />
        </Collapse>
      </Card>
    )
  }
}

MovieCard.propTypes = {
  classes: PropTypes.object.isRequired,
  movie: PropTypes.object.isRequired
}

export default withStyles(styles)(MovieCard)
